export default {
	data() {
		return {
			list: [],
			selectedList: [],
			selectMoney: [],
			isAllChecked: false,
			show: false,
			editValue: '',
			clickMoney: '',
			actId: '',
			orderId: ''
		}
	},
	computed: {
		totalMoney() {
		let num = 0;
		for(let i = 0; i < this.selectMoney.length; i++){
				num = num + this.selectMoney[i];
		}
		return num
		}
	},
	onLoad(options) {
		// console.log(options)
		this.actId = uni.getStorageSync('addActId')
	},
	onShow() {
		this.getList()
	},
	methods: {
		getList() {
			this.$http.get({
				url: '/v1/rest/manage/sceneChargeDetails',
				data: {
					activitiesId: this.actId
				}
			}).then(resp => {
				// console.log(resp)
				if(resp.status == 200) {
					this.list = resp.data
				}
			})
		},
		editMoney(item) {
			// console.log(item)
			if(item.status) {
				uni.showToast({
					title: '该会员已收费，无法修改！',
					duration: 2000,
					icon: 'none'
				});
			}else{
				this.show = true
				this.clickMoney = item.money
				this.orderId = item.orderId
			}
		},
		onChange(v) {
			this.editValue = v.detail
		},
		onClose() {
			this.show = false
		},
		confirm1() {
			this.show = false
			this.$http.get({
				url: '/v1/rest/manage/updateSceneFee',
				data: {
					orderId: this.orderId,
					totalFee: this.editValue
				}
			}).then(resp => {
				// console.log(resp)
				if(resp.status == 200) {
					this.getList()
					uni.showToast({
						title: '修改成功',
						duration: 2000,
						icon: 'none'
					});
				}
			})
		},
		handleCheck(item) {
			this.Item = item
			if(!item.status) {
				let selectedIndex = this.selectedList.indexOf(item.orderId)
				 if(selectedIndex >= 0) {
					this.selectedList.splice(selectedIndex, 1)
					this.selectMoney.splice(selectedIndex, 1)
				}else{
					this.selectedList.push(item.orderId)
					this.selectMoney.push(item.totalFee)
				}
				if(this.selectedList.length == this.list.length) {
					this.isAllChecked = true
				}else{
					this.isAllChecked = false
				}
			}
		},
		// 全选
		allCheck() {
			this.isAllChecked = !this.isAllChecked
			if(!this.isAllChecked) {
				this.selectedList = []
				this.selectMoney = []
			}else{
				this.selectedList = []
				this.selectMoney = []
				this.list.forEach(function(item){
					if(!item.status){
						this.selectedList.push(item.orderId)
						this.selectMoney.push(item.totalFee)
					}
				},this)
			}
		},
		// 添加会员
		addMember() {
			uni.navigateTo({
				url: '/pages/groupOwnerManage/searchMember/searchMember'
			})
		},
		submit() {
			this.$http.get({
				url: '/v1/rest/manage/settlementOk',
				data: {
					orderId: this.selectedList.join(','),
					operatorId: uni.getStorageSync('userInfo').userId,
					clubId: uni.getStorageSync('clubId')
				}
			}).then(resp => {
				console.log(resp)
				if(resp.status == 200) {
					const that = this
					uni.removeStorageSync('addActId')
					uni.showModal({
						title: '提示',
						content: `${resp.data.message}\n收入：${resp.data.expenditure}元\n账户余额：${resp.data.income}元`,
						cancelText: '去结算',
						success: function (res) {
							if (res.confirm) {
								that.getList()
							} else if (res.cancel) {
								uni.redirectTo({
									url: '/pages/groupOwnerManage/activitySettlement/activitySettlement'
								})
							}
						}
					});
				}
			})
		}
	}
}
